<template>
    <div class="card-list">
        <h3>已签约的本人他行账户</h3>
        <fund-card v-for="item in dataForm.cardList" :key="item.id" :data="item" @card-query-click="getSelect( item )">
        </fund-card>
    </div>
    
</template>

<script>

import * as service from '@/services/bank/fund-transfer';

export default {
    data() {
        return {
            dataForm: {
                cardList: []
            }
        };
    },
    created(){
        service.getFundTransferCardQueryIndex().then((res) => {
            this.dataForm.cardList = res.data.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        getSelect (item){
            this.$ls.setItem('queryfundCard', item);
            this.$router.push('/card-query/info');
        }
    },
    watch: {
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateCardQuerySearch', this.dataForm );
        }else{
            this.$store.dispatch('clearCardQuerySearch');
        }
        next( true );
    }
};
</script>

<style lang="scss" scoped>
    .card-list
    {
        padding: 0px 20px;
        h3
        {
            padding-left: 30px;
            line-height: 76px;
            font-size: 13px;
            color: #666666;
        }
        .fund-card
        {   
            margin-bottom: 30px;
        }
    }
</style>



