<template>
    <div class="selector-page my-payee" :class="{ 'show-checkbox': editor  }">
        <better-scroll ref="payeeList" :data="payeeList" 
        :probeType=" probeType " :listenScrollStart=" listenScrollStart "  :listenScroll=" listenScroll " @scrollStart="payeeListScrollStart"  @scroll="payeeListScroll" > 
            <div class="payee-content">
                <search-bar></search-bar>
                <div class="group-list payee-list">
                    <template v-if=" list.length > 0 " v-for="( list, section ) in payeeListSection">
                        <h4 class="division" key=" section " :data-anchor=" section " >{{section}}</h4>
                        <ul class="list" >
                            <li class="item payee-item" v-for=" ( item, index ) in  payeeListSection[section] " :key=" section + index "  >
                                <a href="javascript:;" class="btn-checkbox" :class="{ checked: checkList.indexOf( item.id ) > -1 }"  @click="payeeCheck( item.id )">
                                    <i class="icon"></i>
                                </a>
                                <a href="javascript:;" class="btn-item" @click="payeeClick( item )">
                                    <i class="logo icon-bank-ahrcu"></i>
                                    <div class="attr">
                                        <cite>{{ item.name }} <i class="tag" v-if=" item.tranType ">{{ item.tranType }}</i></cite>
                                        <span>{{ item.account }}</span>
                                        <span>{{ item.bankName }}</span>
                                    </div>
                                </a>
                                <a href="javascript:;" class="btn-delete" @click="payeeDelete( item.id )">
                                    <cite>删除</cite>
                                </a>
                            </li>
                        </ul>
                    </template>
                </div>
            </div>
        </better-scroll>
        <div class="shortcut" ref="shortcut">
            <ul class="list">
                <li class="item" :class="[{ active : item == anchorActive }, item ]" v-for=" ( item, index ) in anchorList" :data-anchor="item">{{ item === 'search' ? '' : item }}</li>
            </ul>
        </div>
         <div v-if="editor" class="fixed-ft">
            <a href="javascript:;" class="btn-checkbox" :class="{ checked: ( checkList.length > 0 && payeeList.length === checkList.length ) }" @click="payeeCheckAll()">
                <i class="icon"></i>
            </a>
            <a href="javascript:;" class="btn-delete" @click=" payeeMoreDelete() ">删除({{ this.checkList.length }})</a>
        </div> 
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import delegate from 'delegate-to';

import * as service from '@/services/bank/selector';
import searchBar from '@/pages/selector/search-bar';

export default {
    data() {
        return {
            routerFrom: '',
            probeType: 3,
            listenScrollStart: true,
            listenScroll: true,
            payeeListScrollStartY: 0,
            payeeList: [],
            payeeListSection: [],
            anchorList: [],
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 76,
            itemHeight: 176,
            anchorHeight: 28,
            btnDeleteWidth: 120,
            searchBarHeight: 84,
            checkList: [],
            editor: false
        };
    },
    created(){
        service.getPayeeList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.list;
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });
            this.payeeList = _temp;
            this._parseData( this.payeeList );
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.payee-content').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
		let _touchDelete = {};
        document.body.addEventListener('touchstart', delegate('.payee-item', e => {
            if (e.touches.length > 0 ) {
                _touchDelete.sx = e.touches[0].clientX;
            }
        }), false);
        document.body.addEventListener('touchmove', delegate('.payee-item', e => {
            if ( this.editor ) {
                return true;
            }
            if (e.touches.length > 0 ) {
                _touchDelete.ex = e.touches[0].clientX;
                if( _touchDelete.sx - _touchDelete.ex > 70 ) {
                    e.delegateTarget.setAttribute('class', 'item payee-item show-delete');
                } else if ( _touchDelete.ex - _touchDelete.sx > 30 ) {
                    e.delegateTarget.setAttribute('class', 'item payee-item');
                }
            }
        }), false);
    },
    computed: {
    },
    components: {
        searchBar
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.payeeList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        _parseData( data ) {
            let _listSection = { 'search': [] };
            let _anchorList = [ 'search' ];
            data.forEach((item, index) => {
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _searchBarHeight = this.$flexible.convert( this.searchBarHeight );
            let _fix = data.length / 3000;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                if( item == 'search' ) {
                    _y -= _searchBarHeight;
                } else {
				    _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                }
                _y += _fix;
            }

            this.payeeListSection = _listSection;
            this.anchorList = _anchorList;
        },
        _hideDelete () {
            document.querySelectorAll('.payee-item.show-delete').forEach((item)=> {
                item.setAttribute('class', 'item payee-item');
            });
        },
        payeeCheck ( id ) {
            let _index = this.checkList.findIndex((item) => { return item == id });
            if( _index === -1 ) {
                this.checkList.push( id );
            } else {
                this.checkList.splice(_index, 1);
            }
        },
        payeeCheckAll () {
            let _temp = [];
            if ( this.checkList.length !== this.payeeList.length ) {
                this.payeeList.forEach((item) => {
                    _temp.push( item.id );
                });
            }
            this.checkList = _temp;
        },
        payeeClick ( data ) {
            if( this.editor ){
                return;
            }
            this.$router.push({ name: 'myPayeeDetail', params: data });
        },
        payeeDelete ( id ) {
            let _index = this.payeeList.findIndex((item) => { return item.id == id });
            if( _index > -1 ) {
                this.payeeList.splice(_index, 1);
            }
            this._parseData( this.payeeList );
            this._hideDelete();
            this.$refs.payeeList.refresh();
        },
        payeeMoreDelete ( ) {
            for (let index = 0; index < this.payeeList.length; index++) {
                if ( this.checkList.findIndex((checkId) => { return checkId == this.payeeList[ index ].id }) > -1 ) {
                    this.payeeList.splice(index--, 1 );
                }
            }
            this.checkList = [];
            this._parseData( this.payeeList );
            this.$refs.payeeList.refresh();
        },
        payeeListScrollStart (data) {
            this.payeeListScrollStartY = 0;
        },
        payeeListScroll (data) {
            this._currentAnchor( data.y );
            // 滚动时，隐藏删除按钮
            if ( this.payeeListScrollStartY - data.y > 20 ) {
                this.payeeListScrollStartY = data.y;
                this._hideDelete();
            }
        },
        showCheckbox () {
            this._hideDelete();
            this.editor = true;
            this.$refs.payeeList.refresh();
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }

};
</script>



<style lang="scss" scoped>

    .search-bar
    {
        height: 84px!important;
        overflow: hidden;
    }

    .btn-checkbox
    {
        position: absolute;
        top: 0px;
        left: -100px;
        height: 100%;
        width: 100px;
        overflow: hidden;

        @include transition(all, 0.3s);
        .icon
        {
            position: absolute;
            top: 50%;
            left: 50px;
            width: 44px;
            height: 44px;

            @include image-set('../../../assets/img/bank/my-payee/checkbox');

            @include bg-offset-center();

            @include transform(translateY(-50%));
        }   

        &.checked
        {
            .icon
            {
                @include image-set('../../../assets/img/bank/my-payee/checkbox-checked');
            }   
        }
    }
    
    .my-payee.show-checkbox
    {
        padding-bottom: 100px;
        .payee-list
        {
            .list
            {
                .item
                {
                    .btn-item
                    {
                        left: 100px;
                    }
                    .btn-checkbox
                    {
                        left: 0px;
                    }
                }
            }
        }

        .fixed-ft
        {
            bottom: 0px;
        }
    }
    .payee-list
    {
        .list
        {
            .item
            {
                position: relative;
                background-color: #fff;
                height: 176px;
                overflow: hidden;
                @include mini-border(top);
                &:before
                {
                    left: 50px;
                }
                
                .btn-item
                {
                    position: relative;
                    display: block;
                    padding: 20px 0px 0px 144px;
                    height: 176px;
                    overflow: hidden;

                    @include transition(all, 0.3s);
                    
                    
                    .logo
                    {
                        left: 50px;
                    }
                    
                    .attr
                    {
                        cite,span
                        {
                            display: block;
                        }
                        cite
                        {
                            font-size: 13px;
                            line-height: 50px;
                        }
                        span
                        {
                            line-height: 44px;
                            font-size: 16px;
                            color: #272727;
                        }
                        span + span
                        {
                            margin-top: 6px;
                            line-height: 36px;
                            font-size: 13px;
                            color: #B5B5B5;
                        }

                        .tag
                        {
                            padding: 0px 10px;
                            line-height: 28px;
                            margin-left: 20px;
                            font-size: 11px;
                            color: #FFFFFF;
                            font-style: normal;
                            background: #B7B7B7;
                            border-radius: 2px;
                        }
                    }
                }

                .btn-delete
                {
                    position: absolute;
                    top: 0px;
                    bottom: 0px;
                    right: -160px;
                    padding-top: 65px;
                    width: 160px;
                    text-align: center;
                    background-image: linear-gradient(127deg, #FF511A 0%, #FF942F 100%);
                    overflow: hidden;

                    @include transition(all, 0.3s);

                    cite
                    {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        font-size: 16px;
                        color: #FFFFFF;

                        @include transform(translate(-50%, -50%));
                    }
                }
            }

            .item:first-child
            {
                &:before
                {
                    content: none;
                }
            }
            .item.show-delete
            {
                .btn-item
                {
                    @include transform(translateX(-160px));
                }
                .btn-delete
                {
                    right: 0px;
                }
            }
        }
    }

    .fixed-ft
    {
        position: absolute;
        bottom: -120px;
        left: 0px;
        width: 100%;
        height: 100px;
        background-color: #fff;

        box-shadow: 0 0 10px 0 rgba(0,0,0,0.11);

        .btn-checkbox
        {
            left: 0px;
        }

        .btn-delete
        {
            position: absolute;
            top: 0px;
            right: 0px;
            padding: 0px 30px;
            min-width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
            font-size: 15px;
            color: #FD4C23;
        }
    }

    .shortcut
    {
        top: 200px;
    }
    
    
</style>



